<template>
  <div>
    <el-menu class="leftNav" unique-opened>
      <el-submenu
        :index="index + ''"
        v-for="(item, index) in leftNavData"
        :key="index"
      >
        <template slot="title">{{ index }}</template>
        <el-submenu
          :index="index2 + ''"
          v-for="(item2, index2) in item"
          :key="index2"
        >
          <template slot="title">{{ index2 }}</template>
          <el-menu-item
            :index="index2 + item3"
            v-for="(item3, index3) in item2"
            :key="index3"
            @click="NavClick(index, index2, item3)"
            >{{ item3 }}</el-menu-item
          >
        </el-submenu>
      </el-submenu>
    </el-menu>

    <div
      style="padding-top: 2vh; margin-left: 13vw; height: 92vh; overflow: auto"
    >
      <h2 style="color: white">
        店铺：{{ firstNav }}-{{ twiceNav }}-{{ threeNav }}
      </h2>
      <div style="color: white; margin-top: 2vh">
        快捷搜索：<select
          name=""
          id=""
          v-model="fastCheck"
          style="width: 8vw; height: 2.5vh; outline: none"
          @change="getEachDateTable"
        >
          <option value="15天内">15天内</option>
          <option value="30天内">30天内</option>
          <option value="60天内">60天内</option>
        </select>
        起始时间：<input
          type="date"
          class="beginDate"
          @change="getDateTable()"
          style="color: black"
        />
        结束时间：<input
          type="date"
          class="endDate"
          @change="getDateTable()"
          style="color: black"
        />
      </div>
      <table class="mailOrderTable">
        <thead>
          <tr>
            <th>序号</th>
            <th>站点</th>
            <th>平台</th>
            <th>国家</th>
            <th>在售sku</th>
            <th>邮件量</th>
            <th>订单量</th>
            <th>邮件/订单比率</th>
            <th>邮件比率阈值</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(item, index) in tableData"
            :key="index"
            @click="getChart(item.country, item.site, item.platform, item.sku)"
            style="cursor: pointer"
          >
            <td>{{ index + 1 + (page - 1) * 50 }}</td>
            <td>{{ item.site }}</td>
            <td>{{ item.platform }}</td>
            <td>{{ item.country }}</td>
            <td>{{ item.sku }}</td>
            <td>{{ item.email_num }}</td>
            <td>{{ item.sku_num }}</td>
            <td
              :class="{
                trbackground:
                  parseInt(item.email_num) / parseInt(item.sku_num) > 0.1
              }"
            >
              {{
                isNaN(
                  (
                    (parseInt(item.email_num) / parseInt(item.sku_num)) *
                    100
                  ).toFixed(2)
                )
                  ? 0
                  : (
                      (parseInt(item.email_num) / parseInt(item.sku_num)) *
                      100
                    ).toFixed(2)
              }}
              %
            </td>
            <td>10%</td>
          </tr>
        </tbody>
      </table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="page"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pagetotle"
        style="margin: 1vh 0 0 25vw"
      >
      </el-pagination>
      <div
        ref="chart"
        style="
          width: 57vw;
          height: 376px;
          padding-right: 5vw;
          margin: 3vh 0 0 14vw;
          background: rgba(255, 255, 255, 0.9);
        "
      ></div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      tableData: [], //表格数据
      leftNavData: [], //左边导航栏
      firstNav: "全部", //一级导航
      twiceNav: "", //二级导航
      threeNav: "", //三级导航
      total: 0, //页码当前页
      page: 1, //
      fastCheck: "",
      xAxis: [
        //控制月数
        {
          type: "category",
          data: [],
          axisPointer: {
            type: "shadow"
          }
        }
      ],
      series: [
        //控制数据
        {
          name: "邮件量",
          type: "bar",
          data: []
        },
        {
          name: "订单量",
          type: "bar",
          data: []
        },
        {
          name: "比率",
          type: "line",
          yAxisIndex: 1,
          data: []
        }
      ]
    };
  },
  methods: {
    getDefaultData() {
      //获取左侧导航栏并赋值
      axios({
        url: this._url2 + "server/customer_management/email_analysis",
        method: "get",
        params: {
          page: 1
        }
      }).then(res => {
        // this.leftNavData = res.data.left_data;
        this.tableData = res.data.data_list;
        this.total = res.data.data_count[0].count / 5;
        console.log(res, 9);
      });
    },
    NavClick(index, index2, item3) {
      //点击左边导航栏事件
      this.firstNav = index;
      this.twiceNav = index2;
      this.threeNav = item3;
      document.querySelector(".beginDate").value = "";
      document.querySelector(".endDate").value = "";
      axios({
        url: this._url2 + "server/customer_management/email_analysis",
        method: "get",
        params: {
          platform: this.firstNav,
          country_code: this.twiceNav,
          site: this.threeNav,
          page: 1
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.data_list;
        this.$nextTick(() => {
          $(".mailOrderTable>tbody").scrollTop(0);
        });
        this.total = res.data.data_count[0].count / 5;
        this.page = 1;
        this.fastCheck = "";
        document.querySelector(".beginDate").value = "";
        document.querySelector(".endDate").value = "";
      });
    },
    getChart(country, site, platform, sku) {
      //点击表格数据更改图表
      axios({
        url: this._url2 + "server/customer_management/email_analysis",
        method: "get",
        params: {
          country: country,
          site: site,
          platform: platform,
          sku: sku
        }
      }).then(res => {
        console.log(res);
        this.xAxis[0].data = [];
        this.series[0].data = [];
        this.series[1].data = [];
        this.series[2].data = [];
        // console.log(res.data.data_list)
        for (let i = 0; i < res.data.data_list.length; i++) {
          if (res.data.data_list[i].date) {
            this.xAxis[0].data[i] = res.data.data_list[i].date;
            this.series[0].data[i] = res.data.data_list[i].email_num;
            this.series[1].data[i] = res.data.data_list[i].sku_num;
            this.series[2].data[i] = (
              res.data.data_list[i].email_num / res.data.data_list[i].sku_num
            ).toFixed(2);
          } else {
          }
        }
        this.getEchartData();
      });
    },
    getDateTable() {
      //时间选择更改表格
      if (this.firstNav == "全部") {
        this.firstNav = null;
      }
      let begin = document.querySelector(".beginDate").value;
      let end = document.querySelector(".endDate").value;
      console.log(begin, end, 888);
      console.log(this.threeNav, this.twiceNav, this.firstNav, 1);
      axios({
        url: this._url2 + "server/customer_management/email_analysis",
        method: "get",
        params: {
          platform: this.firstNav,
          country_code: this.twiceNav,
          site: this.threeNav,
          page: 1,
          begin_date: begin,
          over_date: end
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.data_list;
        this.$nextTick(() => {
          $(".mailOrderTable>tbody").scrollTop(0);
        });
        this.total = res.data.data_count[0].count / 5;
        this.page = 1;
        this.fastCheck = "";
      });
      if (this.firstNav == null) {
        this.firstNav = "全部";
      }
    },
    getEachDateTable() {
      //最近时间更改表格
      if (this.firstNav == "全部") {
        this.firstNav = null;
      }
      let demo = this.fastCheck.substring(0, 2);
      axios({
        url: this._url2 + "server/customer_management/email_analysis",
        method: "get",
        params: {
          platform: this.firstNav,
          country_code: this.twiceNav,
          site: this.threeNav,
          page: 1,
          recent_date: demo
        }
      }).then(res => {
        console.log(res.data.data_list);
        this.tableData = res.data.data_list;
        this.$nextTick(() => {
          $(".mailOrderTable>tbody").scrollTop(0);
        });
        this.total = res.data.data_count[0].count / 5;
        this.page = 1;
        document.querySelector(".beginDate").value = "";
        document.querySelector(".endDate").value = "";
      });
      if (this.firstNav == null) {
        this.firstNav = "全部";
      }
    },
    getEchartData() {
      // 获取div
      // 获取div
      const chart = this.$refs.chart;
      if (chart) {
        //初始化实例
        const myChart = this.$echarts.init(chart);
        const option = {
          tooltip: {
            trigger: "axis",
            axisPointer: {
              type: "cross",
              crossStyle: {
                color: "#999"
              }
            }
          },
          toolbox: {
            feature: {
              dataView: { show: true, readOnly: false },
              magicType: { show: true, type: ["line", "bar"] },
              restore: { show: true },
              saveAsImage: { show: true }
            }
          },
          legend: {
            data: ["邮件量", "订单量", "比率"]
          },
          xAxis: this.xAxis,
          yAxis: [
            {
              type: "value",
              name: "邮件量",
              min: 0,
              max: 1000,
              interval: 100,
              axisLabel: {
                formatter: "{value} "
              }
            },
            {
              type: "value",
              name: "比率",
              min: 0,
              max: 1,
              interval: 0.1,
              axisLabel: {
                formatter: "{value} "
              }
            }
          ],
          series: this.series
        };
        myChart.setOption(option);
        window.addEventListener("resize", function() {
          myChart.resize();
        });
      }
      this.$on("hook:destoryed", () => {
        window.removeEventListener("resize", function() {
          myChart.resize();
        });
      });
    },
    handleCurrentChange(newPage) {
      // 页码切换会触发此函数
      console.log("换页");
      if (this.firstNav == "全部") {
        this.firstNav = null;
      }
      this.page = newPage;
      let begin = document.querySelector(".beginDate").value;
      let end = document.querySelector(".endDate").value;
      if (begin != "") {
        //如果用户选择了用起始时间筛选
        axios({
          url: this._url2 + "server/customer_management/email_analysis",
          method: "get",
          params: {
            platform: this.firstNav,
            country_code: this.twiceNav,
            site: this.threeNav,
            page: this.page,
            begin_date: begin,
            over_date: end
          }
        }).then(res => {
          // console.log(res);
          console.log("打印");
          this.tableData = res.data.data_list;

          this.$nextTick(() => {
            $(".mailOrderTable>tbody").scrollTop(0);
          });
          this.total = res.data.data_count[0].count / 5;
        });
      } else {
        //起始时间没有值
        if (this.fastCheck) {
          //且快捷筛选有值
          let demo = this.fastCheck.substring(0, 2);
          axios({
            url: this._url2 + "server/customer_management/email_analysis",
            method: "get",
            params: {
              platform: this.firstNav,
              country_code: this.twiceNav,
              site: this.threeNav,
              page: this.page,
              recent_date: demo
            }
          }).then(res => {
            // console.log(res);
            this.tableData = res.data.data_list;
            this.$nextTick(() => {
              $(".mailOrderTable>tbody").scrollTop(0);
            });
            this.total = res.data.data_count[0].count / 5;
          });
        } else {
          axios({
            //如果起始时间以及快捷筛选都没有值
            url: this._url2 + "server/customer_management/email_analysis",
            method: "get",
            params: {
              platform: this.firstNav,
              country_code: this.twiceNav,
              site: this.threeNav,
              page: this.page
            }
          }).then(res => {
            // console.log(res);
            this.tableData = res.data.data_list;
            this.$nextTick(() => {
              $(".mailOrderTable>tbody").scrollTop(0);
            });
            this.total = res.data.data_count[0].count / 5;
          });
        }
      }
      if (this.firstNav == null) {
        this.firstNav = "全部";
      }
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    this.getEchartData();
  },
  created() {
    this.getDefaultData();
    axios({
      //左侧导航栏（权限用）
      url: this._url2 + "server/personnel_management/area_sign2/",
      method: "get"
    }).then(res => {
      this.leftNavData = res.data.data;
    });
  }
};
</script>

<style scoped>
table {
  /*width: 84vw;*/
  /* margin: 10px 50px; */
  border-collapse: collapse;
  margin: 2vh 0 0 0vw;
}

tbody {
  display: block;
  max-height: 65vh;
  width: fit-content;
  overflow-y: auto;
  border-bottom: 1px #e5e5e5 solid;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 8vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 2.5vh;
  line-height: 2.5vh;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 2.5vh;
  line-height: 2.5vh;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 15px !important;
}

.leftNav {
  width: 12vw;
  height: 100vh;
  position: fixed;
  left: 0;
}

table tr th:nth-child(1),
table tr td:nth-child(1),
table tr th:nth-child(2),
table tr td:nth-child(2),
table tr th:nth-child(4),
table tr td:nth-child(4),
table tr th:nth-child(6),
table tr td:nth-child(6),
table tr th:nth-child(7),
table tr td:nth-child(7),
table tr th:nth-child(8),
table tr td:nth-child(8),
table tr th:nth-child(9),
table tr td:nth-child(9) {
  width: 7.8vw;
}

table tr th:nth-child(5),
table tr td:nth-child(5) {
  width: 23vw;
}

.trbackground {
  background: red;
}
</style>



